import React from 'react'
import { Link } from 'react-scroll';
import "./TopContent.css";
 import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';

const TopContent = () => {
    return (
         <Container>
        <Row>
            <Col>
            <div className="topcontent">
                <div className="topcontent__container">
                <h1>Mr. Soban</h1>
                <p>A professional Web and App Developer</p>
            <a href="https://drive.google.com/file/d/1x2VxAuPh44FG0nssGMnQW0Xc8oPBdHb5/view?usp=share_link"rel="noreferrer" target="_blank">
                <button className="topContent__dowbtn">Download CV</button>
            </a>
            <Link to="projects" smooth={true} duration={500}>
                <a href="https://github.com/soban2722?tab=repositories">
                <button className="topContent__mywrkbtn">My Works</button>
                </a>
            </Link>
            </div>
            </div>
            </Col>
            </Row>
             </Container>
    );
}

export default TopContent
