
import React from 'react';
import "./Header.css";
import { Link } from 'react-scroll';
import { Container } from 'react-bootstrap';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';


const Header = () => {
    return (
        <><Container>
                <Row>
                    <Col className="navbar" xs={12} sm={6} md={4}>
                        <h1>Develop<span>er</span></h1>
                    </Col>
                    <Col xs={12} sm={6} md={8}>
                        <nav className="navbar navbar-expand-md navbar-dark">
                            <div className="menu-items">
                                <nav className="navbar-nav ms-auto">
                                    <Link to="about" smooth={true} duration={50}>
                                        <h4>About me</h4>
                                    </Link>
                                    <Link to="skills" smooth={true} duration={50}>
                                        <h4>skills</h4>
                                    </Link>
                                    <Link to="projects" smooth={true} duration={50}>
                                        <h4>projects</h4>
                                    </Link>
                                    <Link to="exp" smooth={true} duration={50}>
                                        <h4>experince</h4>
                                    </Link>
                                    <Link to="contact" smooth={true} duration={50}>
                                        <h4>contact</h4>
                                    </Link>
                                </nav>
                            </div>
                        </nav>
                    </Col>
                </Row>
                </Container>
        </>

    );
}
export default Header;


